import React from 'react';
import PropTypes from 'prop-types';
import ScrollToNext from '@components/ScrollToNext';
import './style.scss';
import Slide from 'react-reveal/Slide';
import Typing from 'react-typing-animation';
import Button from '@material-ui/core/Button';

const AboutPage = (props, context) => {
  const {
    theme: { colorPrimary, colorHighlight, bgPrimary, textPrimary }
  } = context;

  return (
    <div className="about-page" style={{ backgroundColor: bgPrimary }}>
      <style jsx="true">
        {`
          .highlight {
            background-color: ${colorHighlight};
          }
          ::selection {
            background-color: ${colorHighlight};
          }
        `}
      </style>
      <div className="content-grid">
      <Slide right>
        <h1 style={{ color: colorPrimary }}>About</h1>
      </Slide>
        <div className="about-wrapper">
          <div className="about-content" style={{ color: textPrimary }}>
            <p>
              Passionn d'informatique depuis toujours. Dveloppeur web depuis 2 ans en autodidacte, j'ai dcider de me former pour approfondir mes connaissances et comptences dans le langage JavaScript : sur  <span className="highlight">React.js / Node.js / React.native</span> dans une formation intensive "La Capsule coding bootcamp"  Lyon, pour une reconversion professionelle.
            </p>
            <p>
              Actuellement chez LDLC OL en tant que Front-End Developer : React.js <span className="highlight"> Lyon</span>.
                            Passionn et curieux, j'aime dcouvrir et tester de nouveaux concepts. Voici ce que j'utilise actuellement comme stack : <div>
                <Typing speed={250}><i>React.js, Antd, Node.js, dva, axios, echarts-for-react, Express.js, MongoDB.</i></Typing></div>
            </p>
            <p className="text-emoji" style={{ color: colorPrimary }}>
              <Button variant="outlined" size="medium" color="primary" target="_blank" style={{ marginRight: 20 }} href="https://drive.google.com/open?id=1siLkkpFFqINw83K7utN7OM6ADxe9TaQV" >
          Mon CV
        </Button>
              <Button variant="outlined" size="medium" color="primary" target="_blank" href="https://drive.google.com/file/d/0BwFfsH91yIATWC1oU2xLSXRlclRsaFl2angtV3lmTkpBay1v/view?usp=sharing" >
          Certificat La Capsule
        </Button>
            </p>
            {/* <Typing speed={50}>
            PS : Merci  Nol, Benjamin, Franck et Alexis @ La Capsule Academy !
            </Typing> */}
          </div>
        </div>
      </div>
      <ScrollToNext pageSelector=".portfolio-page" />
    </div>
  );
};

AboutPage.contextTypes = {
  theme: PropTypes.any
};

export default AboutPage;



